.loadingDataIntro{
    height: 200px;
    width: 350px;
    margin-bottom: 100px;
    margin: auto;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    img{
        max-height: 100%;
        max-width: 100%;
    }
}

.selection {
    height: auto;
    margin-bottom: 30px;
    width: 100%;
    background-color: black;
    border-style: solid;
    border-color: transparent;
    padding: 1%;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 20px;
    button {
      height: 40px;
      width: 125px;
      cursor: pointer;
      border: none;
      transition-property: all;
      transition-timing-function: linear;
      transition-duration: 0.3s;
    }
    .selected {
      background-color: yellow;
      color: black;
    }
    .non_selected {
      background-color: black;
      color: yellow;
    }
}

.genderKey {
    color: #ffffff;
    margin-bottom: 5px;
}

.table {
    h1{
      color: rgb(218, 218, 218);
      margin: 30px 0;
      text-align: center;
    }
    marquee{
      color: white;
      // max-height: 200px;
      // width: 100%;
    }
    height: auto;
    width: 100%;
    padding: 2%;
    background-color: #121212;
    background-color: black;
    table {
      thead {
        background-color: #121212;
      }
      width: 100%;
      height: auto;
      text-align: left;
      color: white;
      border: solid 2px yellow;
      border-collapse: collapse;
      tr:nth-child(even) {
        background-color: #121212;
        cursor: pointer;
      }
      th {
        padding: 15px;
        text-align: left;
        // border: solid 2px yellow;
      }
      td {
        padding: 15px;
        text-align: left;
      }
    }
}
  
@media screen and (max-width:768px){
    .selection {
      height: auto;
      margin-bottom: 30px;
      width: 100%;
      background-color: black;
      border-style: solid;
      border-color: transparent;
      padding: 1%;
      display: flex;
      justify-content: center;
      align-items: center;
      column-gap: 20px;
      button {
        height: 40px;
        width: 125px;
        cursor: pointer;
        border: none;
        transition-property: all;
        transition-timing-function: linear;
        transition-duration: 0.3s;
      }
      .selected {
        background-color: yellow;
        color: black;
      }
      .non_selected {
        background-color: black;
        color: yellow;
      }
    }

    .genderKey {
        color: #ffffff;
        margin-bottom: 5px;
    }

    .table {
      h1{
        color: rgb(218, 218, 218);
        margin: 30px 0;
        text-align: center;
      }
      marquee{
        color: white;
        font-size: 14px;
        font-style: italic;
        margin-bottom: 10px;
        // max-height: 200px;
        // width: 100%;
      }
      height: auto;
      max-width: 100%;
      padding: 2%;
      background-color: #121212;
      background-color: black;
      table {
        max-width: 100% !important;
        font-size: 14px;
        thead {
          background-color: #121212;
        }
        width: 100%;
        height: auto;
        text-align: left;
        color: white;
        border: solid 2px yellow;
        border-collapse: collapse;
        tr:nth-child(even) {
          background-color: #121212;
          cursor: pointer;
          max-width: 100% !important;
        }
        th {
          padding: 15px;
          text-align: left;
          // border: solid 2px yellow;
        }
        td {
          padding: 15px;
          text-align: left;
        }
      }
    }
    
}